.div{
  
  background-color: rgb(39, 32, 32);
  height: 30rem;
  width: 20rem;
  display: flex;
  flex-direction: column;
  padding: 2.1rem;
  margin-top: 3rem;
  
}
.tex{
  color: #fff;
}
body{
  width: 100vw;
  height: 100vh;
  background-color: rgb(13, 29, 13);
  display: flex;
  align-items: center;
  justify-content: center;
}
.ter{
  margin-top: -3rem;
}
@media screen and (max-width: 480px) {
  body {
    background-color: rgb(13, 29, 13);
    width: 100vw;
    height: 90vh;
    display: flex;
  align-items: center;
  justify-content: center;
  }
  .div{
    margin: auto ;
    height: 28rem;
    width: 20rem;
    margin-top: 2rem;
    
  }
  .ter{
    margin-top: 2rem;
  }
}
